import styleInject from '#style-inject';styleInject(".container {\n  width: 100%;\n  padding-right: 1%;\n  overflow: hidden;\n}\n.container .text {\n  position: relative;\n  color: #ed0101;\n  font-size: 6vh;\n  font-weight: 600;\n}\n.container .text.first-text {\n  color: #fff;\n}\n.text.sec-text::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  right: 0;\n  height: 100%;\n  width: 100%;\n  background-color: #131a25;\n  animation: typing 1s steps(10) forwards, removeMask 0s linear 1s forwards;\n}\n.text.sec-text::after {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  border-left: 2px solid #4070f4;\n  animation: cursorMove 1s steps(10) forwards, cursorFlash 0.7s infinite;\n}\n@keyframes cursorMove {\n  to {\n    left: 100%;\n  }\n}\n@keyframes typing {\n  to {\n    width: 0%;\n  }\n}\n@keyframes cursorFlash {\n  0%, 100% {\n    opacity: 1;\n  }\n  50% {\n    opacity: 0;\n  }\n}\n@keyframes removeMask {\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n")